import React from 'react';
import logo from './logo.svg';
import './App.css';
import {BrowserRouter, HashRouter, Redirect, Route, Switch} from "react-router-dom";

function App() {
  return (
    <BrowserRouter>
        <Switch>
          <Route path="/foo">FOO</Route>
          <Route path="/bar">
            <Route path="/baz">Baz</Route>
            Bar
          </Route>
          <Redirect to="foo" />
        </Switch>
    </BrowserRouter>
  );
}

export default App;
